.footer {
    background-color: $background;
    padding-top: 56px;
    @media screen and (min-width: 768px) {
        padding-top: 79px;
    }
    @media screen and (min-width: 1280px) {
        padding-top: 82px;
    }
}

.footer__container {
    @media screen and (max-width: 767px) {
        margin-bottom: 44px;
    }
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        margin-bottom: 91px;
    }
}

.footer__title {
    color: $lable-color;
    @include font(400, 18px, 1.16, 0);
    font-family: Titan One;
    text-transform: uppercase;
    @media screen and (min-width: 768px) {
        @include font(400, 26px, 1.15, 0.04em);
    }
    @media screen and (min-width: 1280px) {
        @include font(400, 30px, 1.13, 0.04em);
    }
}

.footer--accent {
    color: $accent-color;
    @include font(400, 34px, 1.14, 0.04em);
    text-transform: uppercase;
    font-family: Titan One;
    display: block;
    margin-top: 17px;
    @media screen and (min-width: 768px) {
        @include font(400, 48px, 1.14, 0.06em);
        margin-top: 16px;
    }
    @media screen and (min-width: 1280px) {
        @include font(400, 58px, 1.13, 0.06em);
        margin-top: 15px;
    }
}

.footer__title-box {
    @media screen and (max-width: 767px) {
        width: 234px;
        margin-bottom: 43px;
    }
    @media screen and (max-width: 1279px) {
        max-width: 340px;
    }
}

.social {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 46px;
}

.social__item {
    margin-right: 24px;
    &:last-child {
        margin-right: 0;
    }
}

.social__link {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: $white-text;
    background-color: $logo-color;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
        background-color: $accent-color;
        color: $white-text;
        transform: scale(1.5);
        transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
    }
}

.social__icon {
    fill: currentColor;
    width: 20px;
    height: 20px;
}

.social__text {
    @include font(700, 12px, 1.86, 0);
    text-align: right;
    color: $black-text;
    @media screen and (min-width: 768px) {
        @include font(700, 14px, 1.86, 0);
    }
    @media screen and (min-width: 1280px) {
        @include font(700, 16px, 1.86, 0.02em);
    }
}

.social__tell {
    color: $accent-color;
}

.copyright__box {
    border-top: 1px solid #e1e1e1;
    display: flex;
    justify-content: center;
}

.copyright {
    @include font(700, 14px, 1.52, 0);
    padding-top: 20px;
    padding-bottom: 20px;
    color: $primary-text-color;
    @media screen and (max-width: 767px) {
        max-width: 145px;
    }
    @media screen and (min-width: 768px) {
        @include font(700, 14px, 1.86, 0.02em);
        max-width: 280px;
    }
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
        transform: scale(1.25);
        color: $accent-color;
    }
}

@import 'https://fonts.googleapis.com/css?family=Russo+One';
h2.funny-title {
    border-top: 5px solid #e0dbde;
    border-bottom: 5px solid #e0dbde;
    font-size: 70px;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 5px;
    text-transform: uppercase;
    font-family: 'Russo One', sans-serif;
    font-weight: 900;
    letter-spacing: 8px;
}

@-webkit-keyframes move-background {
    0% {
        background-position: -200% 0;
    }
    100% {
        background-position: 0 200%;
    }
}

@-moz-keyframes move-background {
    0% {
        background-position: -200% 0;
    }
    100% {
        background-position: 0 200%;
    }
}

@keyframes move-background {
    0% {
        background-position: -200% 0;
    }
    100% {
        background-position: 0 200%;
    }
}

.section-title {
    -webkit-animation-play-state: running;
    -moz-animation-play-state: running;
    animation-play-state: running;
    color: currentColor;
    background: url(/images/cloud-bg-footer.jpeg) no-repeat $accent-color;
    background-size: cover;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    -webkit-animation: move-background 20s infinite linear alternate;
    -moz-animation: move-background 20s infinite linear alternate;
    animation: move-background 20s infinite linear alternate;
}